@import url(https://fonts.googleapis.com/css?family=Abel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background: #006DA1;
  z-index: 1000;
  box-shadow: 0 0 5px #666;
}
.brand {
  width: 120px;
  height: 80px;
  padding: 0 5px;
  display: block;
  position: absolute;
  background: #fff url('/assets/logo.png') no-repeat center center;
  background-size: 90%;
  z-index: 100;
  text-indent: -9999px;
  top: 60px;
  left: 20px;
}
.site-title {
  color: #fff;
  font-weight: 600;
  font-size: 18px;
  position: absolute;
  left: 15px;
  top: 10px;
}
.site-title:hover {
  color: #fff;
  opacity: 0.8;
}
.mapboxgl-popup-content {
  padding: 20px !important;
  font-size: 18px;
}
.popup-field {
  margin: 0 0 10px 0;
}
.ant-btn-primary {
  background-color: #52AC2C !important;
  border: none !important;
}
.ant-btn-primary:hover {
  opacity: 0.8;
}
.logo {
  width: 250px;
  height: 50px;
  position: absolute;
  left: 12px;
  top: 10px;
  text-indent: -9999px;
  background: url('/assets/logo.png') no-repeat center center;
  background-size: contain;
}
h1 {
  color: #333 !important;
  font-weight: 600 !important;
  position: absolute;
  left: 40px;
  top: 40px;
  font-size: 34px;
  z-index: 1000;
}
h2 {
  font-size: 30px;
  color: #333 !important;
  font-weight: 600 !important;
  padding-bottom: 10px;
  border-bottom: 1px solid #ccc;
}
h3 {
  color: #333 !important;
  font-weight: 600 !important;
  font-size: 14px;
}
.data-switch label {
  display: block;
}
.data-switch {
  text-align: right;
}
.legend {
  position: absolute;
  bottom: 40px;
  padding: 20px 20px 20px 60px;
  right: 40px;
  z-index: 100;
  background: #fff;
  box-shadow: 0 0 5px #999;
}
.legend .recent-ring {
  width: 30px;
  position: absolute;
  left: 20px;
  top: 15px;
  height: 30px;
  border: 4px solid #E59216;
  border-radius: 50%;
}
.data-table-wrapper {
  width: 100%;
  border-top: 1px solid #ccc;
}
.data-table {
  width: 80%;
  margin: 40px auto 40px auto;
}
.data-table-value {
  cursor: pointer;
}
.data-table-row {
  color: #777;
  text-align: center;
  padding: 5px 0 5px 0;
  font-size: 14px;
  min-width: 800px;
  border-bottom: 1px solid #ccc;
}
.data-table-row h3 {
  font-size: 16px;
  margin: 4px 0 0 0;
  text-align: right;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.data-table-row span.value {
  display: block;
  background: #fff;
  font-weight: 600 !important;
  color: #006DA1;
  padding: 5px 5px;
  border: 1px solid #4DABE5;
  border-radius: 3px;
  text-align: center;
  overflow: hidden;
}
.data-table-row span.value.no-data {
  border: 1px solid #ccc;
  color: #ccc;
}
.data-table-row span.value.value-year-2017 {
  color: #E59216;
  border: 1px solid #E59216;
}
.data-table-row span {
  white-space: nowrap;
}

.studies-link {
  position: absolute;
  right: 90px;
  top: 12px;
  color: #fff;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
}
.data-link {
  position: absolute;
  right: 20px;
  top: 12px;
  color: #fff;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
}
.ant-dropdown a {
  color: #111 !important;
}
.ant-popover {
  max-width: 200px;
}
.about-link {
  position: absolute;
  right: 180px;
  top: 12px;
  color: #fff;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
}
.ant-btn-primary {
  font-weight: 600 !important;
  background: #4DABE5 !important;
}
.zoom-control {
  margin-top: 50px;
}
.data-filters {
  text-align: left;
}
.data-table h3 {
  white-space: nowrap;
}
.filters {
  box-shadow: 0 0 5px #999;
  background: #fff;
  padding: 15px 20px 10px 20px;
  position: absolute;
  right: 80px;
  top: 60px;
  z-index: 100;
  color: #000;

}
.filters label {
  display: block;
  font-weight: 600;
}
.footer {
  max-width: 1000px;
  padding: 80px 40px;
  margin: 0 auto;
}
.luxgov {
  display: block;
  position: absolute;
  z-index: 100;
  left: 150px;
  bottom: 20px;
  height: 80px;
  width: 250px;
  border: 10px solid #fff;
  text-indent: -9999px;
  background: #fff url('/assets/luxgov.png') no-repeat center center;
  background-size: contain;
}
.munich {
  display: block;
  position: absolute;
  z-index: 100;
  left: 420px;
  bottom: 20px;
  height: 80px;
  width: 200px;
  border: 10px solid #fff;
  text-indent: -9999px;
  background: #fff url('/assets/munich.png') no-repeat center center;
  background-size: contain;
}
.legal {
  display: block;
  text-align: right;
}


body {
  font-family: 'Abel', sans-serif;
  background: #fff;
}

